import React from "react";
import { Ztabs } from "zerod";
import { Input, message } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/Ztabs/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			tabPanes = [
				{ tab: "基本信息", key: "1", content: "基本信息内容" },
				{ tab: "配置信息", key: "2", content: "配置信息内容" },
				{ tab: "其他信息", key: "3", content: ()=>{return "其他内容"} },
			];
			render() {
				return <Ztabs tabPanes={this.tabPanes} />;
			}
		}
		return <Myjavascript />;
	},
});
